#root,
.App {
  min-width:100%;
  min-height: 100%;
  background-color: #F6F8FA;
}

.title {
  text-align: center;
  margin-bottom: 20px;
}

.demo-container {
  position: fixed;
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
}



.controls-container {
  display: flex;
  flex-direction: column;
  flex-basis: 1;
  overflow-y: scroll;
}

.state-container {
  display: none;
  flex-direction: column;
  flex-basis: 30%;
  border-top: 1px solid black;
}
.state-container > h4 {
  color: black;
}

.state {
  width: 100%;
  height: 100%;
}

.column-right {
  height: 100%;
  width: 100%;
}

.tree-container {
  height: 100%;
}

.prop-container {
  padding: 2px 5px;
  margin: 15px 10px;
}
.prop {
  font-weight: bold;
  display: block;
}

.prop-large {
  font-size: 16px;
}

.sub-prop {
  padding: 5px;
}

.btn-controls {
  color: #f7fafc;
  background-color: #2d3748;
  transition: color 0.15s ease, border-color 0.15s ease;
}
  
.btn-controls:hover,
.btn-controls:focus,
.btn-controls:active {
  color: white;
  border-color: #f7fafc;
}

.tree-stats-container {
  text-align: center;
  padding: 0.5rem 2rem;
  border-bottom: 1px solid #2d3748;
  font-weight: bold;
}

/* Custom path classes */
.my-path-class {
  stroke: royalblue;
  stroke-width: 10;
}


/* Custom node classes */
.demo-node > circle {
  stroke: none;
}

.circle {
  fill:  #404076 !important;
  stroke: rgb(82, 82, 91) !important;
}

.rd3t-node {
  stroke: none !important;
  fill: rgb(82, 82, 91) !important;
  box-shadow: 20px 20px red !important;

}

.rd3t-leaf-node {
  stroke: none !important;
  fill: rgb(82, 82, 91) !important;
}

.rd3t-label__title {
  /* fill: ; */
  stroke: none !important;
  font-weight: 500;
}

.modal {
  position: absolute;
  /* z-index: 100; */
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 300px;
  height: 300px;
}








